.program-selection__wrapper {
  width: $mobile-width;

  padding: 0 20px;
  padding-top: 27px;

  position: relative;

  @media (min-width: $tablet-width) {
    width: $tablet-width;

    padding: 0 70px;
    padding-top: 64px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;

    padding: 0 110px;
    padding-top: 73px;
  }
}

.program-selection__title {
  @include indent-reset;
  font: $font-title-large-mobile;

  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    font: $font-title-large-tablet;

    margin-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 56px;
  }
}

.program-selection__text {
  @include indent-reset;

  font: $font-slogan-title-mobile;
  line-height: 1.28;
  color: $color-1;
  text-transform: uppercase;

  width: 226px;

  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    font: $font-slogan-title-tablet;
    line-height: 1.5;

    width: 323px;

    margin-bottom: 66px;
  }
}

.program-selection__fields-wrapper {
  margin-bottom: 45px;

  @media (min-width: $tablet-width) {
    margin-bottom: 71px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;

    margin-bottom: 65px;
  }
}

.program-selection__fields-container {
  &--information {
    margin-bottom: 46px;

    &:invalid {
      .program-selection__field-input {
        outline: 2px solid $background-color-12;
      }
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 72px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }
  }

  &--program {
    @media (min-width: $tablet-width) {
      width: 628px;

      padding: 25px 28px 29px;
      border: 1px solid $background-color-11;
    }

    @media (min-width: $desktop-width) {
      width: 568px;
    }
  }
}

.program-selection__field-label {
  margin-bottom: 14px;

  position: relative;

  &--program {
    min-height: 30px;

    &:not(:last-child) {
      margin-bottom: 13px;
    }

    &:hover {
      cursor: pointer;
    }

    &:hover .program-selection__control-mark {
      border-color: $background-color-14;
    }

    @media (min-width: $tablet-width) {
      min-height: 40px;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &:nth-of-type(1) {
    .program-selection__field-text {
      min-width: 39px;
    }
  }

  &:nth-of-type(2) {
    .program-selection__field-text {
      min-width: 60px;
    }
  }

  &:nth-of-type(3) {
    .program-selection__field-text {
      min-width: 94px;
    }
  }

  &:nth-of-type(1) {
    .program-selection__field-input {
      width: 228px;

      @media (min-width: $tablet-width) {
        width: 570px;
      }

      @media (min-width: $desktop-width) {
        width: 509px;
      }
    }
  }

  &:nth-of-type(2) {
    .program-selection__field-input {
      width: 207px;

      @media (min-width: $tablet-width) {
        width: 542px;
      }

      @media (min-width: $desktop-width) {
        width: 481px;
      }
    }
  }

  &:nth-of-type(3) {
    .program-selection__field-input {
      width: 173px;

      @media (min-width: $tablet-width) {
        width: 500px;
      }

      @media (min-width: $desktop-width) {
        width: 439px;
      }
    }
  }
}

.program-selection__field-label {
  &--program[disabled] {
    pointer-events: none;
    opacity: 0.3;
  }
}

.program-selection__field-input {
  align-self: center;
}

.program-selection__control-mark {
  &--program {
    display: block;

    width: 30px;
    height: 30px;

    border: 1px solid $background-color-8;
    border-radius: 50%;
    background-color: $background-color-2;

    position: absolute;
    top: 0;
    left: 0;

    @media (min-width: $tablet-width) {
      width: 40px;
      height: 40px;
    }

    &::after {
      content: "";
      display: none;

      width: 15px;
      height: 15px;

      border-radius: 50%;

      background-color: $background-color-3;

      position: absolute;
      top: 7px;
      left: 6px;

      @media (min-width: $tablet-width) {
        width: 18px;
        height: 18px;

        top: 10px;
        left: 10px;
      }
    }
  }
}

.program-selection__field-text {
  display: block;

  margin-right: 13px;

  &--program {
    margin-left: 45px;
    margin-right: 0;

    @media (min-width: $tablet-width) {
      margin-left: 60px;
    }
  }
}

.program-selection__field-input {
  padding: 14px 20px;

  border: 1px solid $background-color-11;

  &--program {
    @include indent-reset;
    width: auto !important; //чтобы радио-кнопки были специфичнее

    position: absolute;
    top: 7px;
    left: 6.5px;
    z-index: 1;

    visibility: hidden;
  }
}

/* Радио-кнопки */
.program-selection__field-input[type="radio"]:checked
  + .program-selection__control-mark::after {
  display: block;
}
